body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{font-family:Arial,Helvetica,sans-serif}.container{margin-top:5vh;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:repeat(7, 1fr);grid-gap:20px 30px;gap:20px 30px;width:50vw}@media screen and (max-width: 480px){.container{display:flex;flex-direction:column;justify-content:center;align-items:center;width:85vw;font-size:1.15rem}}@media screen and (min-width: 480px)and (max-width: 768px){.container{display:flex;flex-direction:column;justify-content:center;width:85vw;font-size:1.15rem}}.container__item--password{position:relative}.container__show-button{position:absolute;color:gray;top:55%;right:3%;background:transparent;border:none;text-decoration:underline;cursor:pointer}.container__item{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start}@media screen and (max-width: 480px){.container__item{width:100%}}.container__item--info{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;grid-area:3/1/5/2}.container__item--confirm{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-area:5/1/7/3}@media screen and (max-width: 480px){.container__item--confirm{position:relative;display:flex;flex-direction:column;justify-content:flex-end;align-items:center}}.container__item--confirm:hover{cursor:pointer}.container__box{font-family:"Cambay",sans-serif;padding:10px;width:100%;height:40px;border:1px solid #d3d3d3;font-size:1rem}@media screen and (max-width: 480px){.container__box{width:85vw}}@media screen and (min-width: 480px)and (max-width: 768px){.container__box{width:85vw}}.container__box--phone{font-family:"Cambay",sans-serif;font-size:1rem;width:60%;height:40px;border:1px solid #d3d3d3;padding:10px}@media screen and (min-width: 480px)and (max-width: 768px){.container__box--phone{width:50vw}}.container__box--info{font-family:"Cambay",sans-serif;font-size:1rem;width:100%;height:110px;padding-left:10px;padding-top:10px;padding-bottom:70px;border:1px solid #d3d3d3;resize:none}@media screen and (min-width: 480px)and (max-width: 768px){.container__box--info{width:85vw}}.container__label{margin-bottom:.6em;font-size:.8rem;font-family:"Cambay",sans-serif}.container__button{position:absolute;display:block}@media screen and (max-width: 480px){.container__button{position:inherit;width:85vw}}@media screen and (min-width: 480px)and (max-width: 768px){.container__button{position:inherit;width:50vw}}.container__button-text{position:absolute;color:#fff;font-weight:600;top:50%;left:50%;transform:translate(-50%, -50%)}@media screen and (max-width: 480px){.container__button-text{top:50%;left:50%}}
.spinnerIcon{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh}.spinnerIcon__image{animation:rotation 1.5s infinite linear;width:5vw;height:auto}@media screen and (max-width: 480px){.spinnerIcon__image{width:10vw;margin-bottom:20vh}}@media screen and (min-width: 480px)and (max-width: 768px){.spinnerIcon__image{width:20vw;margin-bottom:20vh}}@keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}
.complete{display:flex;flex-direction:column;justify-content:space-around;align-items:center;height:25vh;width:30vw}@media screen and (max-width: 480px){.complete{width:80vw;height:30vh}}.complete__content{text-align:center;font-family:"Cambay",sans-serif;font-size:1.2rem}.complete__image{width:6vw;height:auto}@media screen and (max-width: 480px){.complete__image{width:15vw;height:auto}}
*{margin:0;padding:0;box-sizing:border-box}.signIn{position:relative;height:100vh;width:100vw;overflow:hidden;background-color:#f8f5f0;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:0px 0px;gap:0px 0px}@media screen and (min-width: 480px)and (max-width: 768px){.signIn{display:flex;flex-direction:column;justify-content:flex-start;align-items:center}}.signIn__header{font-family:"Noto Sans",sans-serif;font-weight:1200;margin-top:5vh;display:flex;justify-content:center;align-self:flex-start;grid-area:1/2/2/3}.signIn__form{margin-top:7vh;grid-area:1/2/2/3}.signIn__form--inactive{display:none}.signIn__spinner--active{display:flex;justify-content:center;align-items:center;grid-area:1/2/2/3}.signIn__spinner--inactive{display:none}.signIn__complete--active{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;margin-top:15vh;grid-area:1/2/2/3}@media screen and (max-width: 480px){.signIn__complete--active{margin-top:5vh;justify-content:space-between}}.signIn__complete--inactive{display:none}.signIn__leftIllustration{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.signIn__leftIllustration--inactive{display:none}.signIn__rightIllustration{display:flex;flex-direction:column;justify-content:center;align-items:center;grid-area:1/3/2/4}.signIn__rightIllustration--inactive{display:none}.signIn__image--right{position:absolute;width:40vw;top:9%;right:-17%}@media screen and (max-width: 480px){.signIn__image--right{position:absolute;width:35vw;top:2%;right:-10%}}@media screen and (min-width: 480px)and (max-width: 768px){.signIn__image--right{position:absolute;width:30vw;top:0%;right:-10%}}.signIn__image--left{position:absolute;top:25%;left:-1%;width:20vw}@media screen and (max-width: 480px){.signIn__image--left{position:absolute;top:5%;left:-2%}}@media screen and (min-width: 480px)and (max-width: 768px){.signIn__image--left{position:absolute;top:3%;left:-2%}}
